"use client";

import { useState, useRef, useEffect } from "react";
import { Avatar } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { ThumbsUp, ThumbsDown, RefreshCw, Copy, Plus, Send, Star, Settings } from "lucide-react";
import TypeWriter from "@/components/TypeWriter";
import { demoResponses } from "@/lib/demoResponses";

interface Message {
  id: string;
  content: string;
  isUser: boolean;
  timestamp: string;
  isStreaming?: boolean;
}

export default function Home() {
  const [messages, setMessages] = useState<Message[]>([
    {
      id: "1",
      content: "Hello! How can I assist you today?",
      isUser: false,
      timestamp: "16:58",
    },
  ]);
  const [responseIndex, setResponseIndex] = useState(0);
  const [inputValue, setInputValue] = useState("");
  const [isTyping, setIsTyping] = useState(false);
  const chatContainerRef = useRef<HTMLDivElement>(null);

  // Auto-scroll to bottom when messages change
  useEffect(() => {
    if (chatContainerRef.current) {
      chatContainerRef.current.scrollTop = chatContainerRef.current.scrollHeight;
    }
  }, [messages]);

  const handleSendMessage = () => {
    if (!inputValue.trim()) return;

    // Create a new user message
    const newMessage: Message = {
      id: Date.now().toString(),
      content: inputValue,
      isUser: true,
      timestamp: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }),
    };

    setMessages((prev) => [...prev, newMessage]);
    setInputValue("");
    setIsTyping(true);

    // Simulate AI response after a delay
    setTimeout(() => {
      const aiResponse: Message = {
        id: (Date.now() + 1).toString(),
        content: "I'm a simulated AI response. This is a clone of the Mistral AI chat interface.",
        isUser: false,
        timestamp: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }),
        isStreaming: true, // Mark as streaming initially
      };

      setMessages((prev) => [...prev, aiResponse]);
      setIsTyping(false);
      
      // We keep the streaming state active until the typewriter effect completes
    }, 1500);
  };

  return (
    <div className="flex flex-col h-screen bg-background">
      {/* Admin button */}
      <header className="p-4 flex items-center border-b border-border">
        <div className="flex space-x-2">
          <button className="p-2 hover:bg-secondary rounded-full">
            <Plus className="h-5 w-5" />
          </button>
          <button className="p-2 hover:bg-secondary rounded-full">
            <Star className="h-5 w-5" />
          </button>
        </div>
        <button className="ml-auto p-2 hover:bg-secondary rounded-full">
          <Settings className="h-5 w-5" />
        </button>
      </header>

      {/* Chat container */}
      <div
        ref={chatContainerRef}
        className="flex-1 overflow-auto p-4 space-y-6"
      >
        {messages.map((message) => (
          <div key={message.id} className={`flex ${message.isUser ? 'justify-end' : 'justify-start'}`}>
            <div className={`${message.isUser ? 'chat-message-user' : 'chat-message-ai'}`}>
              {!message.isUser && (
                <div className="flex-shrink-0">
                  <Avatar className="h-8 w-8 bg-primary text-primary-foreground" />
                </div>
              )}
              <div className="flex flex-col">
                <div className="text-sm">
                  {!message.isUser && message.isStreaming ? (
                    <TypeWriter 
                      text={message.content} 
                      onComplete={() => {
                        setMessages(prevMessages => 
                          prevMessages.map(msg => 
                            msg.id === message.id 
                              ? { ...msg, isStreaming: false } 
                              : msg
                          )
                        );
                      }} 
                    />
                  ) : (
                    message.content
                  )}
                </div>
               </div>
             </div>
           </div>
         ))}

         {isTyping && (
           <div className="flex justify-start">
            <div className="chat-message-ai items-center">
               <Avatar className="h-8 w-8 bg-primary text-primary-foreground">
              </Avatar>
               <div className="flex items-center space-x-1">
                 <div className="h-2 w-2 bg-foreground/70 rounded-full animate-pulse"></div>
                 <div className="h-2 w-2 bg-foreground/70 rounded-full animate-pulse delay-150"></div>
                 <div className="h-2 w-2 bg-foreground/70 rounded-full animate-pulse delay-300"></div>
               </div>
            </div>
          </div>
        )}
      </div>

      {/* Input area */}
      <div className="p-4 border-t border-border">
         <div className="flex items-center space-x-2 max-w-3xl mx-auto">
           <Input
            value={inputValue}
            onChange={(e) => setInputValue(e.target.value)}
            onKeyDown={(e) => e.key === 'Enter' && handleSendMessage()}
            placeholder="Ask a question..."
            className="chat-input"
          />
          <Button
            onClick={handleSendMessage}
            disabled={!inputValue.trim()}
            className="rounded-full p-2 h-10 w-10 flex items-center justify-center"
            variant={inputValue.trim() ? "default" : "secondary"}
          >
            <Send className="h-5 w-5" />
          </Button>
        </div>
      </div>
    </div>
  );
}
